.app{display:grid;grid-gap:32px;grid-template-rows:64px 1fr 72px;min-height:100vh;width:100%}.app__main{display:grid;grid-template-columns:20px minmax(600px, 1080px) 20px;grid-template-rows:1fr;margin:0 auto;position:relative}.app__main>.browser{grid-column:2 / 3;min-height:600px}.app__main .app__firefox{background-color:var(--grey-10);background-image:url(images/flat-firefox-e5633fecf3822aed4979007c497a9e13.svg);background-position:center center;background-repeat:no-repeat;background-size:164px 164px;flex:1}
