.modal{align-items:center;background:var(--grey-90-a60);bottom:0;display:none;justify-content:center;left:0;position:fixed;right:0;top:0;z-index:999}.modal--display{display:flex}.modal__content{border-radius:var(--large-unit-border-radius);box-shadow:var(--large-unit-box-shadow);background-color:var(--white-a96);align-items:center;animation:translate-in 250ms forwards ease-out;background:var(--white-100);box-shadow:0 10px 20px var(--grey-90-a20);display:flex;flex-direction:column;justify-content:center;line-height:1.5rem;min-height:300px;padding:16px;position:relative;text-align:center;width:500px;will-change:transform}.modal__toggle{color:var(--grey-90);background-color:rgba(12,12,13,0.1);align-items:center;border-radius:3px;border:0;cursor:pointer;display:flex;height:40px;justify-content:center;position:absolute;right:12px;top:12px;width:40px}.modal__toggle:hover:not(:disabled),.modal__toggle:focus:not(:disabled){background-color:rgba(12,12,13,0.2)}.modal__toggle:active:not(:disabled){background-color:rgba(12,12,13,0.3)}.modal__toggle:disabled{color:var(--grey-40);cursor:default}@keyframes translate-in{0%{opacity:0;transform:translate3d(0, 120px, 0)}100%{opacity:1;transform:translate3d(0, 0, 0)}}
