.app-header{align-items:center;background:var(--grey-10);display:flex;height:64px;justify-content:space-between;padding:0 15px;position:relative;width:100%;z-index:2}.app-header .app-header__content{align-items:center;display:flex;margin:0 auto;width:100%}.app-header .app-header__icon{background-image:url(images/logo-77da51ec1afe6bbd083b1953cf1862f6.svg);background-position:center 1px;background-repeat:no-repeat;background-size:32px auto;height:32px;margin-right:10px;width:32px}.app-header h1{font-size:18px;letter-spacing:-.2px;margin:0;text-align:left}.app-header h3{font-size:12px;font-weight:normal;letter-spacing:0;margin:0;text-align:left}.app-header h3 a{line-height:13px;text-decoration:none}.app-header .app-header__controls{display:flex;align-items:center}.app-header .app-header__spacer{background:var(--grey-90-a10);height:46px;width:1px;margin:10px}.app-header .app-header__button{align-items:center;background:transparent;border:0;cursor:pointer;display:flex;flex-direction:column;height:64px;justify-content:center;padding:0;width:64px}.app-header .app-header__button .app-header__button-icon{align-items:Center;display:flex;height:20px;margin-bottom:1px;width:22px}.app-header .app-header__button.disabled .app-header__button-icon,.app-header .app-header__button.disabled span{opacity:.5}.app-header .app-header__button:not(.disabled):hover{background:var(--grey-90-a05)}.app-header .app-header__button:not(.disabled):active{background:var(--grey-90-a10)}.app-header .app-header__survey{color:var(--white);background:var(--blue-50);align-items:center;border-radius:2px;cursor:pointer;display:flex;font-size:14px;height:28px;justify-content:flex-end;overflow:hidden;padding:0 5px 0 7px;text-decoration:none;transition:size 125ms, width 200ms;user-select:none;white-space:nowrap;margin-left:15px}.app-header .app-header__survey:hover:not(:disabled),.app-header .app-header__survey:focus:not(:disabled){background-color:var(--blue-60)}.app-header .app-header__survey:active:not(:disabled){background-color:var(--blue-70)}.app-header .app-header__survey:disabled{opacity:.5;cursor:disabled}.app-header .app-header__survey img{margin:0 1px 0 7px}@media (max-width: 720px){.app-header header{display:none}}
