.theme-pattern-picker{display:flex;flex-direction:column;flex:1;padding:12px 0}.theme-pattern-picker p{text-align:center;margin-top:10px}.theme-pattern-picker .theme-pattern-picker__inner{display:grid;grid-template-columns:1fr 1fr;grid-auto-rows:64px;width:100%;grid-gap:24px;overflow:auto;padding:12px 24px;margin-bottom:12px}.theme-pattern-picker .theme-pattern-picker__pattern{border-radius:3px;overflow:hidden;transition:box-shadow 150ms}.theme-pattern-picker .theme-pattern-picker__pattern:hover{box-shadow:0 0 0 1px var(--white-100),0 0 0 3px var(--grey-30)}.theme-pattern-picker .theme-pattern-picker__pattern.active{box-shadow:0 0 0 1px var(--white-100),0 0 0 3px var(--blue-40)}.theme-pattern-picker .theme-pattern-picker__color{width:100%;height:100%}
