.theme-colors-editor{display:flex;justify-content:space-between;width:100%;height:100%}.theme-colors-editor__list{display:grid;grid-template-rows:repeat(5, 44px);grid-gap:4px;grid-template-columns:1fr 1fr;margin:0;padding:16px;position:relative;flex:1 1 640px;grid-auto-flow:column}.theme-colors-editor__list .theme-unit{align-items:center;display:flex;padding:0 16px;cursor:pointer;transition:background 150ms}.theme-colors-editor__list .theme-unit:hover{background:var(--grey-90-a05)}.theme-colors-editor__list .theme-unit.selected .theme-unit__label{color:var(--blue-50)}.theme-colors-editor__list .theme-unit.selected .theme-unit__picker{display:block}.theme-colors-editor__list .theme-unit.selected .theme-unit__swatch{box-shadow:0 0 0 2px var(--blue-50)}.theme-colors-editor__list .theme-unit__label{flex:1}.theme-colors-editor__list .theme-unit__swatch{border-radius:2px;border:1px solid var(--grey-90-a30);height:20px;width:20px;margin-right:16px}.theme-colors-editor__picker{flex:0 0 270px;border-left:1px solid var(--grey-30);display:flex;justify-content:center;align-items:flex-start}.theme-colors-editor__picker .theme-colors-editor__prompt{align-items:center;background:var(--grey-10);display:flex;flex:1;height:100%;justify-content:center;padding:12px}.theme-colors-editor__picker .theme-colors-editor__prompt p{flex:0 0 170px}.theme-colors-editor__picker .theme-colors-editor__prompt-arrow{width:24px;height:24px;background:url(images/arrowhead-left-77c369c28d52b17a30021f85c59a4844.svg);margin-right:16px}.sketch-picker{box-sizing:border-box !important;box-shadow:none !important;border-radius:0 !important}
